<template>
  <div class="attention_container">
    <div class="attention_header">
      <div class="attention_wrap">
        <div>&lt;</div>
        <div>我的关注</div>
      </div>
    </div>
    <div class="attention_main_container">
      <div class="attention_item" v-for="(item,index) in attentionData.list" :key="index">
        <div class="item_image">
          <img :src="item.avatar" alt />
        </div>
        <div class="item_info">
          <div class="info_top">
            <span style="color:#595959">{{item.teacher_name}}</span>&nbsp;&nbsp;
            <span style="color:#ea7a2f;font-size:12px;">{{item.level_name}}</span>
          </div>
          <div class="info_bottom">
            <div class="ellisp">{{item.introduction}}</div>
          </div>
        </div>
        <div class="item_cancel">
          <button @click="cancelClick">取消按钮</button>
        </div>
      </div>
      <div
        style="font-size:13px;color:#595959;text-align:center;line-height:30px;width:100%;heihgt:30p;"
      >没有更多了</div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      attentionData: [],
      collectID: Number
    };
  },
  computed: {},
  methods: {
    //   取消关注
    cancelClick() {
      this.$http
        .put(`/api/app/collect/cancel/${this.collectID}/2`)
        .then(resp => {
          console.log(resp.data);
          this.$http
            .get("/api/app/collect?page=1&limit=10&type=2&")
            .then(resp => {
              resp.data.data.list.forEach(ele => {
                this.collectID = ele.collect_id;
              });
              this.attentionData = resp.data.data;
            });
        });
    }
  },
  mounted() {
    this.$http.get("/api/app/collect?page=1&limit=10&type=2&").then(resp => {
      console.log(resp.data.data.list);
      resp.data.data.list.forEach(ele => {
        this.collectID = ele.collect_id;
      });
      this.attentionData = resp.data.data;
    });
  }
};
</script>
<style  scoped>
.attention_container {
  width: 100%;
}
.attention_header {
  width: 100%;
  height: 45px;
  display: flex;
  justify-content: flex-start;
  align-items: Center;
  color: #595959;
}
.attention_wrap {
  width: 61%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.attention_main_container {
  width: 100%;
  height: 658px;
  background: #f0f2f5;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.attention_item {
  width: 100%;
  height: 108px;
  background: #fff;
  margin-top: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.item_image {
  width: 50px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.item_image img {
  width: 40px;
  height: 40px;
}
.item_info {
  width: 70%;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: Center;
  padding-left: 10px;
  box-sizing: border-box;
}
.info_top {
  width: 100%;
  height: 35px;
}
.info_bottom {
  width: 100%;
  height: 35px;
}
.ellisp {
  width: 100%;
  height: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #b7b7b7;
  font-size: 13px;
  line-height: 30px;
}
.item_cancel {
  width: 67%;
  height: 100%;
  display: flex;
  font-size: 15px;
  justify-content: center;
  align-items: Center;
}
.item_cancel button {
  width: 60px;
  height: 45px;
  border: none;
  color: #eb6100;
  border-radius: 5px;
  background: rgba(235, 97, 0, 0.1);
}
</style>